import { t } from "i18next"
import { useSignUp } from "@clerk/clerk-react"
import { OAuthStrategy } from "@clerk/types"
import { Button, Typography } from "@mui/material"
import { AppleLogo, GoogleLogo, MicrosoftLogo } from "@rp/react/common/assets"
import { useClerkSignUpUnsafeMetadata } from "@rp/react/web/common/hooks"

function useSignInWith(strategy: OAuthStrategy) {
  const { signUp } = useSignUp()
  const unsafeMetadata = useClerkSignUpUnsafeMetadata()

  async function handleSignUp() {
    await signUp?.authenticateWithRedirect({
      redirectUrl: "/sso-callback",
      redirectUrlComplete: "/",
      unsafeMetadata,
      strategy,
    })
  }

  return handleSignUp
}

function SocialSignUpButton({
  strategy,
  Icon,
  label,
}: {
  strategy: OAuthStrategy
  Icon: React.FC
  label: string
}) {
  const handleSignIn = useSignInWith(strategy)

  return (
    <Button
      onClick={handleSignIn}
      variant="contained-light"
      fullWidth
      sx={theme => ({
        display: "flex",
        gap: 1,
        background: theme.palette.grey[800],
        transition: "background 0.3s",
        ":hover": { background: theme.palette.grey[700] },
      })}
    >
      <Icon />
      <Typography variant="body1" color="text.primary" textTransform="none">
        {label}
      </Typography>
    </Button>
  )
}

export const GoogleSignInButton = () => (
  <SocialSignUpButton
    strategy="oauth_google"
    Icon={GoogleLogo}
    label={t("loginPage.socialLoginButton", { media: "Google" })}
  />
)

export const MicrosoftSignInButton = () => (
  <SocialSignUpButton
    strategy="oauth_microsoft"
    Icon={MicrosoftLogo}
    label={t("loginPage.socialLoginButton", { media: "Microsoft" })}
  />
)

export const AppleSignInButton = () => (
  <SocialSignUpButton
    strategy="oauth_apple"
    Icon={() => <AppleLogo color="white" />}
    label={t("loginPage.socialLoginButton", { media: "Apple" })}
  />
)
